.nav-bar {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 99;
  box-shadow: 1px 1px 15px 0 rgba(0, 0, 0, 0.12);
}

.nav-top {
  background-color: #a5c926;
  height: 32px;
  width: 100%;
}

.nav-top-wrapper {
  margin: auto;
  color: #fff;
  font-size: 11px;
  line-height: 16px;
  max-width: 1200px;
  display: flex;
  height: 100%;
  justify-content: space-between;
}

.nav-top-info {
  display: flex;
  align-items: center;
}

.nav-top-text {
  margin-right: 16px;
}

.link-white {
  color: #fff;
}

.nav-top-social {
  display: flex;
}

.nav-top-social a {
  width: 28px;
  height: 32px;
  display: flex;
  align-items: center;
}

.nav-top-social a img {
  width: 18px;
  height: 18px;
  max-width: 100%;
  display: inline-block;
}

.nav-main {
  background-color: #fff;
}

.nav-main .nav-main-wrapper {
  margin: auto;
  max-width: 1200px;
  display: flex;
  height: 72px;
  font-family: 'Open Sans', sans-serif;
}

.nav-main .brand {
  font-family: 'Varela Round', sans-serif;
  font-size: 22px;
  line-height: 32px;
  font-weight: 400;
  text-decoration: none;
  color: #333333;
  display: flex;
  align-items: center;
}

.nav-main .navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 24px;
  flex: 1;
}

.nav-main .navigation .nav-link {
  color: #111;
  font-size: 14px;
  line-height: 24px;
  text-decoration: none;
  padding: 12px 18px;
}

.nav-main .navigation .nav-button a {
  display: flex;
  text-decoration: none;
}

.nav-main .navigation .nav-button a .cart {
  cursor: pointer;
  font-size: 14px;
  color: #111;
}

.nav-main .navigation .nav-button a .item-count {
  height: 20px;
  min-width: 20px;
  padding-top: 1px;
  padding-bottom: 1px;
  border-radius: 10px;
  background-color: #a5c926;
  color: #fff;
  font-size: 12px;
  font-weight: 600;
  text-align: center;
  line-height: 20px;
}
